@import "./active/button-active";
@import "./active/wrapper-active";
@import "./focus/button-focus";

.borderedBtn{
    height:100%;
    width:100%;
}
.borderedBtn-wrapper{
    height:100%;
    width:100%;
    border-radius:22px;
    background: linear-gradient(180deg, $purple 0%, #8BA4F9 100%);
    cursor: pointer;
    transition:all .1s linear;
    @include flexAllCenter();
}
.borderedBtn--button{
    height:90%;
    width:95%;
    padding:0 20px;
    @include flexAllCenter();
    text-transform: uppercase;
    @include MontserratBold();
    border:none;
    border-radius:22px;
    line-height: 15px;
    background: #fff;
    color:$purple;
    cursor: pointer;
    transition:all .1s linear;
    @media screen and (min-width:900px) and (max-width:1199px) {
        font-size:10px;
    }
    @media screen and (min-width:768px) and (max-width:899px) {
        font-size:9px;
    }
}